@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh; //viewport high es la altura de toda la pantalla
    }

    .principal {
        flex: 1; //forzar que tome todo el espacio
        overflow: scroll;
    }

    .contenido {
        padding: 2rem;
        
        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        margin-top: 3rem;
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton(v.$indigo, v.$blanco);
            padding: 1rem;
            text-transform: uppercase;
            font-size: 1.4rem;
        }
    }

    .enlace {
        color: v.$indigo;

        &:hover {
            color: darken(v.$indigo, 20%);
        }
    }
}