@use 'base/variables' as v;
@use 'base/mixins' as m;

.restablecer {
    margin-top: 5rem;
    .projectflow {
        @include m.textoDegradado(v.$azul);
    }

    input[type="submit"] {
        @include m.boton (v.$azul,  v.$blanco);
    }

    .acciones {
        a {
            color: v.$azul;
        }
    }
}    