@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra-mobile {
    background-color: v.$indigo;
    padding: 2rem 4rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    gap: .5rem;

    @include m.tablet {
        display: none;
    }

    h1 {
        color: v.$blanco;
        margin: 0;
        font-size: 3.2rem;
    }

    .menu {
        margin-left: 1rem;
        img {
            width: .8rem;
            &:hover {
                cursor: pointer;
            }
        }
    }

    p {
        font-size: 1.2rem;
        color: v.$blanco;
    }
}

.barra {
    display: none;

        @include m.tablet {
           display: flex;
           justify-content: space-between;
           align-items: center;  
           background-color: v.$blanco;
           padding: 1rem 2rem;
           box-shadow: 1px 12px 9px -13px rgba(0, 0, 0,.4);
        }       

    p {
        font-size: 1.4rem;


        @include m.telefono {
        margin: 0;
        }
       

        span {
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion {
        @include m.boton(darken(v.$indigo, 20%), v.$blanco);
        font-size: 1.2rem;
        padding: 1rem;
        border-radius: .4rem;
    }
}