@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
    margin-bottom: 5rem;
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
    }

    label {
        flex: 0 0 10rem;
    }

    input:not([type="submit"]) {
        flex: 1;
        border: none;
        padding: 1rem;
        background-color: lighten(v.$gris, 40%);
    }
}